<template>
   <div class="wrapper">
       <ul>
           <li>
               <div>unit 1</div>
           </li>
           <li>
               <div>unit 2</div>
           </li>
           <li>
               <div>unit 3</div>
           </li>
           <li>
               <div>unit 4</div>
           </li>
           <li>
               <div @click="showPlayer">展开</div>
           </li>
           <li>
               <div>unit 6</div>
           </li>
           <li>
               <div>unit 7</div>
           </li>
           <li>
               <div>unit 8</div>
           </li>
       </ul>
       <div class="btn">正在播放...</div>
   </div> 
</template>
<script>
export default {
  mounted(){
    $(".btn").on("click",()=>{
        change();
    });
    function change(){
        var wrap = $(".wrapper ul");
        wrap.toggleClass("open");//点击增加class或者删除class
        var oLi = $(".wrapper ul li");
        var len=oLi.length;//获取li的数量
        var deg = 360 / len//li每次将要旋转的角度
        for(var i = 0;i<len;i++){//用for循环遍历每个li
            var d = i* deg;//从第一个li开始，每个li旋转的角度
            wrap.hasClass('open')? rotateZ(oLi[i],d) : rotateZ(oLi[i],-360);
            //判断如果有class为open的类名，则旋转，否则全部倒转360°
        }
    }
    function rotateZ(dom,deg){
        $(dom).css({
            'transform':'rotateZ('+deg +'deg)'//给li附加css样式transform 旋转Z轴
        })
        .find('div').css({
            'transform':'rotateZ('+ -deg +'deg)'//同时子元素div Z轴旋转负角度，这样div始终为水平面
        })
    }
    //拖拽功能(主要是触发三个事件：onmousedown\onmousemove\onmouseup)  
    var drag = document.querySelector(".wrapper");  
  
    //点击某物体时，用drag对象即可，move和up是全局区域，也就是整个文档通用，应该使用document对象而不是drag对象(否则，采用drag对象时物体只能往右方或下方移动)  
    drag.onmousedown = function(e) {  
        var e = e || window.event; //兼容ie浏览器  
        var diffX = e.clientX - drag.offsetLeft; //鼠标点击物体那一刻相对于物体左侧边框的距离=点击时的位置相对于浏览器最左边的距离-物体左边框相对于浏览器最左边的距离  
        var diffY = e.clientY - drag.offsetTop;  
  
        /*低版本ie bug:物体被拖出浏览器可是窗口外部时，还会出现滚动条，  
            解决方法是采用ie浏览器独有的2个方法setCapture()\releaseCapture(),这两个方法，  
            可以让鼠标滑动到浏览器外部也可以捕获到事件，而我们的bug就是当鼠标移出浏览器的时候，  
            限制超过的功能就失效了。用这个方法，即可解决这个问题。注：这两个方法用于onmousedown和onmouseup中*/  
            if(typeof drag.setCapture!='undefined'){  
                drag.setCapture();  
            }  
  
        document.onmousemove = function(e) {  
            var e = e || window.event; //兼容ie浏览器  
            var left=e.clientX-diffX;  
            var top=e.clientY-diffY;  
  
            //控制拖拽物体的范围只能在浏览器视窗内，不允许出现滚动条  
            if(left<0){  
                left=0;  
            }else if(left >window.innerWidth-drag.offsetWidth){  
                left = window.innerWidth-drag.offsetWidth;  
            }  
            if(top<0){  
                top=0;  
            }else if(top >window.innerHeight-drag.offsetHeight){  
                top = window.innerHeight-drag.offsetHeight;  
            }  
  
            //移动时重新得到物体的距离，解决拖动时出现晃动的现象  
            drag.style.left = left+ 'px';  
            drag.style.top = top + 'px';  
        };  
        document.onmouseup = function(e) { //当鼠标弹起来的时候不再移动  
            this.onmousemove = null;  
            this.onmouseup = null; //预防鼠标弹起来后还会循环（即预防鼠标放上去的时候还会移动）  
  
            //修复低版本ie bug  
            if(typeof drag.releaseCapture!='undefined'){  
                drag.releaseCapture();  
            }  
        };  
    };
  },
  methods: {
      showPlayer:function(){
          this.$emit('parent-showP')
      }
  }
}
</script>
<style scoped>
.wrapper{
    position: absolute;
    width: 80px;
    height: 80px;
    top: 100px;
    right: 100px;
    z-index: 3000;
}
.wrapper .btn{
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #C10D0C;
    color: #fff;
    text-align: center;
    line-height: 80px;
    font-size: 12px;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2); 
    cursor: pointer;
    opacity: 0.5;
}
.wrapper .btn:hover{
    background-color: #C10D0C;
    color: orange;
    opacity: 1;
}
.wrapper ul{
    position: absolute;
    height: 100%;
    width: 100%;
}
.wrapper ul li{
    position: absolute;
    height: 100%;
    width: 100%;
    transition: all 0.8s ease-in-out;
}
.wrapper ul li div{
    position: absolute;
    width: 0;
    height: 0;
    font-size: 0;
    border-radius: 50%;
    top: -90px;
    left: 50%;
    background-color: #fff;
    text-align: center;
    line-height: 0px;
    box-shadow: none;
    cursor:pointer;
    color:#C10D0C;
    transition: all 0.8s ease-in-out,color 0.1s,background 0.1s;
    overflow: hidden;
}
.wrapper .open li div{
    width: 50px;
    height: 50px;
    line-height: 50px;
    box-shadow: 0 3px 3px rgba(0,0,0,0.2);
    font-size: 12px;
    margin-left: -28px;
}
.open li div:hover{
    color: orange;
}
</style>



